import GeoJSON from 'ol/format/GeoJSON';
import { Vector as VectorLayer } from 'ol/layer';
import Map from 'ol/Map';
import { Vector as VectorSource } from 'ol/source';
import { Circle as CircleStyle, Fill, Stroke, Style } from 'ol/style';
import { useEffect, useState } from 'react';

export const useFileImporter = (
    map: Map | null,
    filesToLoad: FileList | null
) => {
    const [processedFiles, setProcessedFiles] = useState<Set<string>>(
        new Set()
    );

    useEffect(() => {
        if (!filesToLoad || !map) return;

        const files = Array.from(filesToLoad);

        files.forEach((file: File) => {
            console.log('Processing file:', file.name);
            if (processedFiles.has(file.name)) return;

            const reader = new FileReader();

            if (file.name.endsWith('.geojson') || file.name.endsWith('.json')) {
                reader.onload = (e) => {
                    const result = e.target?.result;
                    if (typeof result === 'string') {
                        try {
                            const features = new GeoJSON().readFeatures(
                                JSON.parse(result),
                                {
                                    featureProjection: map
                                        ?.getView()
                                        .getProjection(),
                                }
                            );

                            const vectorSource = new VectorSource({
                                features: features,
                            });

                            const vectorLayer = new VectorLayer({
                                source: vectorSource,
                                style: new Style({
                                    stroke: new Stroke({
                                        color: '#f59e0b',
                                        width: 2,
                                    }),
                                    fill: new Fill({
                                        color: 'rgba(245, 158, 11, 0.1)',
                                    }),
                                    image: new CircleStyle({
                                        radius: 6,
                                        fill: new Fill({ color: '#f59e0b' }),
                                        stroke: new Stroke({
                                            color: '#fff',
                                            width: 2,
                                        }),
                                    }),
                                }),
                            });

                            map?.addLayer(vectorLayer);

                            // Zoom to extent
                            if (features.length > 0) {
                                const extent = vectorSource.getExtent();
                                map?.getView().fit(extent, {
                                    padding: [50, 50, 50, 50],
                                    duration: 1000,
                                });
                            }

                            setProcessedFiles((prev) =>
                                new Set(prev).add(file.name)
                            );
                        } catch (err) {
                            console.error('Error parsing GeoJSON', err);
                        }
                    }
                };
                reader.readAsText(file);
            } else {
                console.log(
                    `File ${file.name} received. Requires additional parsers.`
                );
            }
        });
    }, [filesToLoad, processedFiles, map]);

    return { processedFiles };
};
